.MainMenu {  
  width: 100vw;
  height: 100vh;
}

#welcome-message {
  margin: auto;
  text-align: center;
  font-size: 2em;
  color: var(--jd-gray);
  text-shadow: var(--menu-shadow);
  line-height: 150%;
}

#welcome-message:hover {
  cursor: default;
}

#navbar {
  position: fixed;
  top: 10%;
  left: 10%;
  width: 80%;
  height: var(--navbar-height);
}

#navbar-left, #navbar-right {
  display: inline-block;
  height: var(--navbar-height);
}

#navbar-left {
  float: left;
}

#navbar-right {
  float: right;
}

#active-view-area {
  position: fixed;
  top: calc(10% + var(--navbar-height) + 15px);
  left: 10%;
  right: 10%;
  bottom: 5%;
  display: -ms-flexbox;
  display: flex;
}

#menu-background {
  width: 100vw;
  height: 100vh;
  z-index: -10;
  background-image: url("/static/bg.png");
  background-repeat: repeat;
  background-size: 300px;
  position: fixed;
  -webkit-animation: 200s scroll infinite linear;
  animation: 200s scroll infinite linear;
  top: 0;
  left: 0;
}

@-webkit-keyframes scroll{
  100%{
    background-position: 3000px -3000px;
  }
}

@keyframes scroll{
  100%{
    background-position: 3000px -3000px;
  }
}



